import React from 'react';
import { graphql } from 'gatsby';
import Layout from "../components/layout";
import SEO from '../components/seo';

const Template = ({ data }) => {
	const title = data.markdownRemark.frontmatter.title;
	const excerpt = data.markdownRemark.frontmatter.excerpt;
	const date = data.markdownRemark.frontmatter.date;
	const html = data.markdownRemark.html;
	const timeToRead = data.markdownRemark.fields.readingTime.text;
	const tags = data.markdownRemark.frontmatter.tags;

	return (
		<Layout>
			<SEO 
				title={title}
				description={excerpt} />
			<div style={{
				display: "flex",
				flexDirection: "row",
				justifyContent: "center",
				flexFlow: "column nowrap",
				margin: "6px 10px"
			}}
			>
				{/* <div style={{background: "red", width: "30%"}}>Left side</div> */}
				<div>
					<h1 style={{
						color: "#26466f"
					}}>{title}</h1>
					<h5>
						<svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-calendar-date" viewBox="0 0 16 16">
							<path fill-rule="evenodd" d="M3.5 0a.5.5 0 0 1 .5.5V1h8V.5a.5.5 0 0 1 1 0V1h1a2 2 0 0 1 2 2v11a2 2 0 0 1-2 2H2a2 2 0 0 1-2-2V3a2 2 0 0 1 2-2h1V.5a.5.5 0 0 1 .5-.5zM1 4v10a1 1 0 0 0 1 1h12a1 1 0 0 0 1-1V4H1z"/>
							<path d="M6.445 11.688V6.354h-.633A12.6 12.6 0 0 0 4.5 7.16v.695c.375-.257.969-.62 1.258-.777h.012v4.61h.675zm1.188-1.305c.047.64.594 1.406 1.703 1.406 1.258 0 2-1.066 2-2.871 0-1.934-.781-2.668-1.953-2.668-.926 0-1.797.672-1.797 1.809 0 1.16.824 1.77 1.676 1.77.746 0 1.23-.376 1.383-.79h.027c-.004 1.316-.461 2.164-1.305 2.164-.664 0-1.008-.45-1.05-.82h-.684zm2.953-2.317c0 .696-.559 1.18-1.184 1.18-.601 0-1.144-.383-1.144-1.2 0-.823.582-1.21 1.168-1.21.633 0 1.16.398 1.16 1.23z"/>
						</svg>{" "}
						{date}
					</h5>
					<h5>
						<svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-clock" viewBox="0 0 16 16">
							<path fill-rule="evenodd" d="M8 15A7 7 0 1 0 8 1a7 7 0 0 0 0 14zm8-7A8 8 0 1 1 0 8a8 8 0 0 1 16 0z"/>
							<path fill-rule="evenodd" d="M7.5 3a.5.5 0 0 1 .5.5v5.21l3.248 1.856a.5.5 0 0 1-.496.868l-3.5-2A.5.5 0 0 1 7 9V3.5a.5.5 0 0 1 .5-.5z"/>
						</svg>
						{" "}
						{timeToRead}
					</h5>
					<div style={{maxWidth: "800px"}} className="blogpost" dangerouslySetInnerHTML={{ __html: html }} />
					<div style={{
						display: "flex"
					}}>
						{tags.map(tag => <div style={{
							padding: "0.3em 0.7em",
							margin: "1em 0.5em",
							background: "lightgrey",
							borderRadius: "0.2em",
							fontSize: "0.8em",
							cursor: "pointer"
						}}>{tag}</div>)}
					</div>
				</div>
			</div>
		</Layout>
	);
};

export const postQuery = graphql`
	query($pathSlug: String!) {
		markdownRemark(frontmatter: { path: { eq: $pathSlug } }) {
			html
			frontmatter {
				title
				date(formatString: "MMMM, DD, YYYY")
				path
				tags
				excerpt
			}
			fields {
				readingTime {
				  text
				}
			}
		}
	}
`;

export default Template;